.TopBar {
  @include topbar();
  justify-content: space-between;
}
.Box {
  width: 100vw;
}
.KeyBox {
    display:flex;
    flex-wrap: wrap;
}
#vertical {
  display: flex;
  flex-direction: vertical;
}
#horizontal {
  display: flex;
  flex-direction: horizontal;
}
@media (min-width: 550px) {
  .MainBox {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
  }
}
@media (max-width: 550px) {
  .MainBox {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
  }
  #space {
    order: 3;
  }
  #enter {
    order: 2;
  }
}
